import DefaultList from '@/components/content/defaultlist/DefaultList.vue'
import { getFriendInfo } from "@/network/addfriend.js"
import { imgBaseUrl } from '@/common/helper.js'
import { mapState } from 'vuex'

export default {
	components: {
		DefaultList
	},
	data() {
		return {
			friendAccount: '',
			myFriendInfo: null,
			imgUrl: '',
			isMe: false
		}
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		
		//跳转到修改备注和标签页
		toChangeRemark() {
			uni.navigateTo({
				url: `/components/content/changefriendinfo/ChangeRemark?friendAccount=${this.myFriendInfo.friendAccount}`
			})
		},
		//预览图片、保存图片
		previewFriendHeadImg() {
			const imgPath = `${imgBaseUrl}${this.myFriendInfo.friendFaceImage}`
			uni.previewImage({
				urls: [imgPath],
				longPressActions: {
					itemList: ['保存到相册'],
					success: res => {
						console.log(res.tapIndex)
						if(res.tapIndex === 0) {
							uni.saveImageToPhotosAlbum({
								filePath: imgPath,
								success: res2 => {
									uni.showToast({
										title: '已保存到系统相册',
										icon: 'none'
									})
								},
								fail: err2 => {
									uni.showToast({
										title: '保存失败，请稍后重试',
										icon: 'none'
									})
								}
							})
						}
					}
				}
			})
		},
		
		//获取好友个人信息
		doGetFriendInfo() {
			getFriendInfo(`?myAccount=${this.userInfo.user.userAccount}&friendAccount=${this.friendAccount}`).then(res => {
				console.log(res)
				if(res.status === 200) {
					if(res.data.code === 2000) {
						this.$nextTick(() => {
							this.myFriendInfo = res.data.data
						})
					}else {
						uni.showToast({
							title: '您可能与服务器断开了连接',
							icon: 'none'
						})
					}
				}else {
					uni.showToast({
						title: '您可能与服务器断开了连接',
						icon: 'none'
					})
				}
			}).catch(err => {
				uni.showToast({
					title: '您可能与服务器断开了连接',
					icon: 'none'
				})
				console.log(err)
			})
		},
		toMyDynamic(params) {
			let account = Number
			if(params === 'myself') {
				account = this.userInfo.user.userAccount
			}else if(params === 'other') {
				account = this.friendAccount
			}
			uni.navigateTo({
				url: `/components/content/dynamic/MyDynamic?account=${account}&friendRemarkName=${this.myFriendInfo.friendRemarkName}`
			})
		}
	},
	onLoad(option) {
		
		//查看自己资料
		if(option.friendAccount === this.userInfo.user.userAccount) {
			this.isMe = true
		}else {
			//查看朋友资料
			this.friendAccount = option.friendAccount
			this.isMe = false
			this.doGetFriendInfo()
		}
	},
	created() {
		this.imgUrl = imgBaseUrl
		uni.$once('changeFriendInfo', () => {
			this.$nextTick(() => {
				this.doGetFriendInfo()
			})
		})
	},
	onNavigationBarButtonTap(option) {
		if(option.index === 0) {
			if(!this.isMe) {
				uni.showActionSheet({
					itemList: ['设置备注和标签', '把他推荐给朋友', '加入黑名单', '删除'],
					success:res => {
						console.log(res)
						const tapIndex = res.tapIndex
						switch(tapIndex) {
							case 0:
								console.log('设置备注和标签')
								break
							case 1:
								console.log('把他推荐给朋友')
								break
							case 2:
								console.log('加入黑名单')
								break
							case 3:
								console.log('删除')
								break
						}
					},
					fail:err => {
					}
				})
			}
		}
	}
}
